@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700);
body, html {
  height: 100%;
  padding: 0;
  margin: 0;
  background-color: #E8E9ED;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2f2f2f;
}
.container {
  margin-top: 60px;
  margin-bottom: 60px;
}
nav .logo {
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  float: left;
  display: block;
  color: #2f2f2f;
  text-align: center;
  padding: 15px 14px;
  text-decoration: none;
}
nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
nav li {
  margin: 0;
  float: left;
  list-style: none;
}
nav li a {
  font-family: "Roboto", sans-serif;
  display: block;
  color: #8E8E8E;
  font-size: 15px;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}
nav a:hover:not(.active) {
  color: #4A4A4A;
}
nav a:hover {
  color: #4A4A4A;
}
.right {
  float: right;
}
.active {
  color: #000000;
}
.vertical_hr {
  border-left: 2px solid #E0E0E0;
  margin-top: 10px;
  height: 32px
}
@media only screen and (max-width: 480px) {
.burger-nav {
    display: block;
    height: 40px;
    /*width: 100%;*/
    /*float: right;*/
    padding-top: 13px;
    color: #000000;
    font-size: 22px;
    cursor: pointer;
    text-align: right;
    margin-right: 25px;
}
.burger-nav:hover {
    color: #000000;
}
nav {
    border: none;
}
nav ul {
    overflow: hidden;
    height: 0;
}
nav ul.open {
    height: auto;
}
nav ul li {
    float: none;
    width: 100%;
}
nav ul li a {
    text-align: right;
}
.vertical_hr {
    display: none;
}
.card {
    margin-top: 25px;
}
}
.card {
  font-family: "Roboto", sans-serif;
  margin-bottom: 60px;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 20px 2px rgba(0, 0, 0, 0.05);
  box-shadow: 0px 0px 20px 2px rgba(0, 0, 0, 0.05);
}
.movie-card {
  font-family: "Roboto", sans-serif;
  background-color: #fff;
  margin-bottom: 30px;
  min-height: 430px;
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 20px 2px rgba(0, 0, 0, 0.05);
  box-shadow: 0px 0px 20px 2px rgba(0, 0, 0, 0.05);
}
.movie-card .title {
  font-weight: 500;
  font-size: 20px;
  color: #1C2329;
  margin-bottom: 25px;
  min-height: 65px;
}
.card-image img{
  border-radius: 10px 10px 0px 0px;
  min-height: 320px;
}
.card-content {
  padding: 20px;
}
.movie-attribute {
  font-weight: 500;
  color: #3B3B3B;
  margin-bottom: 13px;
}
.card-info {
  color: #8E8E8E;
  font-weight: 500;
  font-size: 10px;
  margin-bottom: 5px;
}
.info {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 18px;
}
.info span{
  color: #939393;
  font-size: 13px;
  margin-left: 6px;
}
.info span:hover {
  cursor: pointer;
}
.modal-button, .modal-button:focus {
  background-color: #2f2f2f;
  color: #ebebeb;
  font-family: "Roboto", sans-serif;
  border-color: #2f2f2f;
}
.modal-button:hover {
  background-color: #121212;
  color: #ebebeb;
  border-color: #121212;
}
.modal {
  font-family: "Roboto", sans-serif;
}
.sub {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
}
.video-info p {
  margin: 0;
}
hr {
  margin: 8px;
}
.center-table td, .center-table th{
  margin: 0px auto;
  text-align: center;
}
.center-table td {
  padding-top: 0px;
  padding-bottom: 0px;
}
.center-table button {
  margin-top: 8px;
}
.movie-button, .movie-button:active, .movie-button:focus {
  margin-top: 15px;
  color: #fff;
  background-image: -webkit-gradient(linear, left top, right top, from(#007FFF), to(#00A0FF));
  background-image: linear-gradient(to right, #007FFF, #00A0FF);
  border-color: transparent;
}
.movie-button:hover {
  color: #fff;
  background-image: -webkit-gradient(linear, left top, right top, from(#006EEE), to(#0090EE));
  background-image: linear-gradient(to right, #006EEE, #0090EE);
  border-color: transparent;
}


